import { Component, Element, Host, Prop, State, Watch, h } from '@stencil/core';
import gsap from 'gsap';
import {
  fadein,
  slideleft,
  slideright,
  slideup,
} from './utils/group-animation.helper';
import {
  TpGroupAnimationElement,
  TpGroupAnimationType,
} from './utils/group-animation.type';

@Component({
  tag: 'tp-group-animation',
  styleUrl: 'group-animation.scss',
  scoped: true,
})
export class TpGroupAnimation {
  @Element() host: HTMLElement;

  /**
   * Define a animação dos componentes do grupo.
   */
  @Prop({ reflect: true }) animation: TpGroupAnimationType;

  /**
   * Define se a animação dos componentes será ativada pelo scroll,
   * quando o componente estiver dentro da área de scroll.
   */
  @Prop({ reflect: true }) scrollTrigger = false;

  /**
   * Define a duração das animações de cada componente do grupo
   * em milissegundos.
   */
  @Prop({ reflect: true }) duration = 500;

  /**
   * Define o delay do inicio das animações em milissegundos.
   */
  @Prop({ reflect: true }) delay = 0;

  /**
   * Define o tempo entre o inicio de animações sequenciais
   * em milissegundos.
   */
  @Prop({ reflect: true }) stagger = 0;

  /**
   * Define a porcentagem da área de scroll que o componente deve
   * atingir para dar inicio a sua animação.
   */
  @Prop({ reflect: true }) intersectionTrigger = 0;

  /**
   * Define o seletor html/css da área de scroll.
   */
  @Prop({ reflect: true }) scroller: string = 'ion-content';

  /**
   * O buffer de componenetes que serão animados.
   */
  @State() buffer: TpGroupAnimationElement[] = [];

  /**
   * Observa se o elemento está na viewport.
   */
  observer: IntersectionObserver;

  /**
   * O elemento html da área de scroll.
   */
  scrollContainer: HTMLElement;

  /**
   * A linha do tempo das animações.
   */
  timeline: gsap.core.Timeline = gsap.timeline();

  /**
   * Define se as animações ja foram iniciadas.
   */
  started = false;

  /**
   * Event listeners.
   */
  @Watch('buffer')
  onBufferUpdate() {
    this.play();
  }

  /**
   * Lifecycle hooks.
   */
  componentWillLoad() {
    this.onBeforeComponentLoad();
  }

  disconnectedCallback() {
    this.observer.disconnect();
  }

  /**
   * Métodos internos.
   */
  onBeforeComponentLoad() {
    this.buffer = Array.from(this.host.children).map((element) => ({
      target: element,
      played: false,
      shouldPlay: false,
    }));

    if (this.scrollTrigger) {
      this.scrollContainer = document.querySelector(this.scroller);
      this.setupObserver();
    } else {
      this.buffer.forEach((element) => (element.shouldPlay = true));
      this.play();
    }
  }

  setupObserver() {
    this.observer = new IntersectionObserver(
      this.onObserverTriggered.bind(this),
      {
        root: this.scrollContainer,
        rootMargin: `0% 0% -${this.intersectionTrigger}% 0%`,
        threshold: 0,
      },
    );

    this.buffer.forEach((element) => {
      this.observer.observe(element.target);
    });
  }

  onObserverTriggered(entries: IntersectionObserverEntry[]) {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        const target = entry.target;
        const matchTarget = this.buffer.find((el) => el.target === target);

        if (matchTarget && !matchTarget.played) {
          matchTarget.shouldPlay = true;
          this.observer.unobserve(matchTarget.target);
        }
      }
    });

    this.buffer = [...this.buffer];
  }

  play() {
    const currentBuffer = [
      ...this.buffer.filter((element) => !element.played && element.shouldPlay),
    ];

    let animation: any;
    switch (this.animation) {
      case 'fadein':
        animation = fadein;
        break;

      case 'slideup':
        animation = slideup;
        break;

      case 'slideleft':
        animation = slideleft;
        break;

      case 'slideright':
        animation = slideright;
        break;

      default:
        animation = fadein;
    }

    const activeElements = currentBuffer.map((element) => element.target);
    const delay = this.started ? 0 : this.delay;
    this.timeline.add(
      animation(activeElements, delay, this.stagger, this.duration),
    );
    currentBuffer.forEach((element) => (element.played = true));

    if (!this.started) this.started = true;
  }

  render() {
    return (
      <Host
        class={{
          'tp-animation': true,
          [`tp-animation--${this.animation}`]: true,
        }}
      >
        <slot />
      </Host>
    );
  }
}
